import $ from 'jquery';

export default class Footer {
  element = ''
  tpl = ` <footer>
      <span>&copy;Copyright 2018 ThoughtWorks</span>
    </footer>`
  constructor() {
    this.element = $(this.tpl)
  }
  render() {
    return this.element;
  }
}


